	import token from '../../components/token.vue';
    import {
        mapState
    } from 'vuex'
	
    export default {
		data() {
			return {
				slides: [{
					number: 'static/img/slider/mainPact.svg',
					linkTab: 'pact'
				}, {
					number: 'static/img/slider/mainDaily.svg',
					linkTab: 'daily'
				}, {
					number: 'static/img/slider/mainNearby.svg',
					linkTab: 'nearby'
				}, {
					number: 'static/img/slider/mainPan.svg',
					linkTab: 'pan'
				}, {
					number: 'static/img/slider/mainStore.svg',
					linkTab: 'store'
				}, {
					number: 'static/img/slider/mainGame.svg',
					linkTab: 'game'
				}, {
					number: 'static/img/slider/mainEntertain.svg',
					linkTab: 'entertain'
				}],
				indicatorDots: false,
				autoplay: false,
				interval: 2000,
				duration: 500,
				circular: false,
				currentSwiper: 0,
				tokens: [{
					value: '0.0214'
				}, {
					value: '0.0872'
				}, {
					value: '0.0476'
				}, {
					value: '0.0013'
				}, {
					value: '0.0013'
				}]
			}
		},	
		components: {
			token
		},
		methods: {
			toPan() {
				uni.switchTab({
					url: '../pan/pan'
				});
			},
			linkTo(linkTab) {
				// console.log(linkTab)
				if(linkTab == 'pan') {
					uni.switchTab({
					  url: '../pan/pan'
					});
				} else if (linkTab == 'pact') {
					uni.navigateTo({
					  url:'../pact/pact'
					});
				  }
				  else if (linkTab == 'daily') {
				  	uni.navigateTo({
				  	  url:'../daily/daily'
				  	});
				  }
				  else if (linkTab == 'store') {
					uni.navigateTo({
					  url:'../store/store'
					});
				  }
				  else if (linkTab == 'game') {
					uni.navigateTo({
					  url:'../game/game'
					});
				  }
				  else if (linkTab == 'entertain') {
					uni.navigateTo({
					   url:'../entertain/entertain'
					});
				  }
			}
		}
    }
